"use client";
import Button from '@/ui/Button';
import TextInput from '@/ui/TextInput';
import React, { useState, ChangeEvent } from 'react';

interface FilterParams {
  stateCode: string;
  yearBuilt: string;
  functionalClass: string;
  owner: string;
}

interface Bridge {
  id: number; // Add specific fields as per your API response
  name: string;
  location: string;
}

const BridgeDashboard: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const [filterParams, setFilterParams] = useState<FilterParams>({
    stateCode: '',
    yearBuilt: '',
    functionalClass: '',
    owner: '',
  });

  const [filteredBridges, setFilteredBridges] = useState<Bridge[]>([]);

  const handleFilterChange = (e: ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFilterParams((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleApplyFilters = async () => {
    try {
      setLoading(true);
      const res = await fetch('/api/bridges', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(filterParams),
      });

      if (!res.ok) {
        throw new Error('Failed to fetch bridges');
      }

      const data: Bridge[] = await res.json();
      setFilteredBridges(data);
    } catch (error) {
      console.error('Error fetching bridges:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6">Bridge Dashboard</h1>

      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">Filters</h2>
        <div className="grid grid-cols-2 gap-4">
          <div>
        
            <TextInput
            label='State Code'
              type="number"
              name="stateCode"
              value={filterParams.stateCode}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter State Code"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Year Built</label>
            <input
              type="number"
              name="yearBuilt"
              value={filterParams.yearBuilt}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Year Built"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Functional Class</label>
            <input
              type="number"
              name="functionalClass"
              value={filterParams.functionalClass}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Functional Class"
            />
          </div>
          <div>
            <label className="block text-sm font-medium mb-2">Owner</label>
            <input
              type="number"
              name="owner"
              value={filterParams.owner}
              onChange={handleFilterChange}
              className="w-full px-4 py-2 border rounded-lg"
              placeholder="Enter Owner Code"
            />
          </div>
        </div>
        <div className="mt-6">
          <Button
            onClick={handleApplyFilters}
            label={loading ? "Loading..." : "Apply Filters"}
          />
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Uncomment and pass appropriate props when components are ready */}
        {/* <BridgeList bridges={filteredBridges} /> */}
        {/* <Map bridges={filteredBridges} /> */}
      </div>
    </div>
  );
};

export default BridgeDashboard;
